import { request } from 'utils';

const prefix = '/api/finance';
request.prefix = prefix;

export const getFeeSubmitSubjects = async ({ page, pageSize = 10, orderBy, isPagingEnabled = true }) => {
  const result = await request({
    prefix,
    url: '/FeeObject',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });
  if(result.data && result.data.items) {
    result.data.items.forEach((item, index) => {
      item.index = index + 1;
    });
  }
  return result;
}

export const getFeeSubmitSubjectById = id =>
  request({
    prefix,
    url: `/FeeObject/${id}`,
    method: 'GET',
  });

export const createFeeSubmitSubject = data =>
  request({
    prefix,
    url: "/FeeObject",
    data,
    method: 'POST',
  });
  

export const updateFeeSubmitSubject = data =>
  request({
    prefix,
    url: `/FeeObject/${data.id}`,
    data,
    method: 'PUT',
  });
  

export const deleteFeeSubmitSubject = id => 
  request({
    prefix,
    url: `/FeeObject/${id}`,
    method: 'DELETE',
  });


