import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getFeeSubmitSubjects, deleteFeeSubmitSubject } from 'services/feeSubmitSubjectType';
import './style.scss';

class FeeSubmitSubjectType extends Component {
  state = {
    reload: 0,
  };

  handleDelete = async id => {
    await deleteFeeSubmitSubject(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'STT',
        dataIndex: 'index',
        key: 'index',
        align: 'center',
        width: '100px'
      },
      {
        title: ' Tn i tng',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        className: 'action-column',
        align: 'center',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#fee-submit-subject-type/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Ha n">
        <CartHeadAction>
          <BtnAddNew href="#fee-submit-subject-type/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getFeeSubmitSubjects}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <FeeSubmitSubjectType />
  </Page>
);



